.section-container {
    background: linear-gradient(to top, rgba(255, 255, 255, 1.0), rgba(255, 255, 255, 1.0)), url('../../../assets/images/office.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: scale-down;
    height: 60vh;
    background-color: yellow;
}

.hero-section {
    padding: 100px 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    background: linear-gradient(to left, #003C71, rgb(246, 244, 248));
    background: none;
    width: inherit;

}

.ad-section-container {
    background: linear-gradient(to top, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), url('../../../assets/images/businessman.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: scale-down;
    width: 700px;
    height: 40vh;
}

/* Text section */
.text-section {
    width: 50%;
    padding: 0 32px;
}

.text-headline {
    margin-bottom: 12px;
    color: rgb(77, 75, 75);
    color: white;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: normal;
    letter-spacing: 0;
    text-align: right;
}

.text-title {
    color: var(--app-color-gray-dark);
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 50px;
    font-weight: normal;
    text-align: right;
}

.text-descritpion {
    margin: 32px 0;
    color: black;
    text-align: right;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
    line-height: 1.6rem;
}

.text-appointment-btn {
    padding: 0px 20px;
    color: white;
    border: 1px solid transparent;
    border-radius: 28px;
    outline: transparent;
    background-color: #1A8EFD;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .8px;
    cursor: pointer;
    transition: all .4s ease;
    a {
        color: white;
        text-decoration: none;
    }
}

.text-appointment-btn:hover {
    color: black;
    background-color: transparent;
    border: 1px solid #1A8EFD;
    a {
        color: #1A8EFD;
    }
}

.text-stats {
    margin-top: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 48px;
    margin-bottom: 120px;
}

.text-stats-container {
    text-align: center;
}

.text-stats-container>p {
    color: rgb(77, 75, 75);
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    letter-spacing: .7px;
}

.text-stats-container p:first-child {
    margin-bottom: 8px;
    color: #1A8EFD;
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-weight: bold;
}

/* Image section */
.hero-image-section {
    width: 50%;
    max-width: 100%;
    text-align: center;
}

.hero-image1 {
    width: 100%;
    height: auto;
    opacity: 0.6;
}

.hero-image2 {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0.18;
}

.hero-image3 {
    position: relative;
    width: 100%;
    opacity: 0.18;
}

/* Scroll Btn */
.scroll-up {
    width: 45px;
    height: 45px;
    display: none;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: white;
    border: 3px solid white;
    border-radius: 50%;
    background-color: #1A8EFD;
    position: fixed;
    bottom: 50px;
    right: 50px;
    font-size: 24px;
    z-index: 20;
    cursor: pointer;
}

.show-scroll {
    display: flex;
}

.hero-image1:after {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);

}

/* Responsive */
@media screen and (max-width: 1400px) {


    #container {
        display: none;
    }

    .hero-image3 {
        display: none;
    }

    .text-section {
        width: 100%;
        padding-left: 100px;
    }

    .hero-image-section {
        display: none;
    }
}

@media screen and (max-width: 900px) {
    .text-appointment-btn {
        display: none;
    }

    .hero-section {
        background: linear-gradient(to left, #003C71, rgb(246, 244, 248));
        background: 0;
        padding: 0px 10px;
    }

    .hero-image-section {
        display: none;
    }

    .text-section {
        width: 100%;
        padding: 0;
    }

    .text-title,
    .text-descritpion {
        width: 100%;
        color: black;
    }
}

@media screen and (max-width: 600px) {

    .section-container {
        /* background: linear-gradient(to top, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.9)), url('../../../assets/images/office.jpg'); */
        background-repeat: no-repeat;
        background-size: cover;
        object-fit: scale-down;
    }

    .text-headline {
        font-size: 20px;
    }

    .text-title {
        font-size: 28px;
    }

    .text-descritpion {
        font-size: 16px;
    }

    .text-appointment-btn {
        font-size: 16px;
    }

    .text-stats {
        gap: 18px;
    }

    .text-stats-container>p {
        font-size: 16px;
    }

    .text-stats-container p:first-child {
        font-size: 22px;
    }

    .scroll-up {
        bottom: 32px;
        right: 32px;
    }

    .hero-image3 {
        display: none;
    }
}

