.about-section {
    padding: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    /* background: linear-gradient(to right, #003C71, rgb(246, 244, 248)); */
}

/* Image */
.about-image-content {
    width: 50%;
    max-width: 100%;
    text-align: center;
    padding: 50px;
}

.about-image1 {
    width: 80%;
    height: auto;
}

/* Text */
.about-text-content {
    width: 50%;
}

.about-title {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
}

.about-title>span {
    position: relative;
}

.about-title span::before {
    content: '';
    position: absolute;
    width: 75%;
    height: 4px;
    background-color: #9D2553;
    border-radius: 8px;
    left: 0;
    bottom: -12px;
}

.about-description {
    margin: 32px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
    line-height: 1.7rem;
}

.about-text-title {
    margin: 0 0 24px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: .7px;
}

/* Steps */
.about-text-step {
    margin: 16px 0;
}

.about-text-sTitle {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: .7px;
}

.about-text-description {
    margin: 8px 24px;
    color: #4d4b4b;
    color: black;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .7px;
    line-height: 1.6rem;
}

.fa-icon {
    color: #9D2553;
}

/* Responsive */
@media screen and (max-width: 1000px) {
    .about-title {
        font-size: 32px;
        padding-bottom: 20px;
    }

    .about-section {
        display: block;
        /* background: linear-gradient(to left, #003C71, rgb(246, 244, 248)); */
    }

    .about-image-content {
        display: none;
    }

    .about-image1 {
        width: 100%;
        height: 100%;
    }

    .about-text-content {
        width: 100%;
    }
}