.navbarsection {
    padding: 0 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    width: 100%;
}

.navbartitle a {
    text-decoration: none;
    color: #003C71;

}

.navbartitle {
    font-family: 'Montserrat';
    font-weight: normal;
}

.navbarsign {
    color: #54de54;
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
}

.navbaritems {
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 32px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    padding-right: 20px;
}

.navbaritems>li {
    list-style-type: none;
}

.navbarlinks {
    text-decoration: none;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    letter-spacing: .8px;
    cursor: pointer;
}

.navbarlinks:hover {
    color: #0cc2ea;
}

.navbarbtn {
    padding: 14px 20px;
    color: white;
    border: 1px solid transparent;
    border-radius: 28px;
    outline: transparent;
    background-color: #1A8EFD;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .8px;
    cursor: pointer;
    transition: all .4s ease;
}

.navbarbtn:hover {
    color: #1A8EFD;
    background-color: white;
    border: 1px solid #1A8EFD;
}

/* Hamburger Icon */
.mobilenav {
    display: none;
}

.hambicon {
    width: 26px;
    height: 26px;
    cursor: pointer;
}

.hambicon:hover {
    color: #0cc2ea;
}

/* Mobile Navbar */
.mobilenavbar {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -100%;
    background-color: white;
    z-index: 20;
    justify-content: center;
    align-items: center;
    transition: left .5s ease-in-out;
}

.mobilenavbarclose {
    position: absolute;
    top: 28px;
    right: 28px;
}

.mobilenavbarclose .hambicon:hover {
    color: rgb(255, 22, 22);
}

.opennav {
    left: 0;
}

.mobilenavbarlinks {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    font-size: 26px;
    gap: 24px;
    text-align: left;
}

.mobilenavbarlinks li a {
    text-decoration: none;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 20px;
    letter-spacing: .8px;
    transition: color .3s ease-in-out;
}

.mobilenavbarlinks li a:hover {
    color: #0cc2ea;
    cursor: pointer;
}

