.swiper {
    width: 100%;
    height: 100%;
}

.swiper {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}


.gamesSwiper1 {
    width: 100%;
    height: 800px;
    position: absolute;
    top: 0;
    left: 0;
}

.gamesSwiper1 .swiper-slide img {
    width: 100%;
    object-fit: contain;
}


.gamesSwiper2 {
    height: 200px;
    width: 100%;
    min-width: 2200px;
    position: absolute;
    top: 50vh;
    left: 0;
    padding-left: 20%;
    padding-right: 20%;
    
}

.gamesSwiper2 .swiper-slide img {
    width: 100%;
    object-fit: contain;
}

.gamesSwiper2 .swiper-slide {
    width: 25%;
    height: 100%;
    background-color: transparent;

    p {

        font-family: var(--app-text-mobile-font);
        font-size: 18px;
        font-weight: normal;
        text-align: left;
        color: var(--app-color-gray-dark);

    }

    progress {
        height: 5px;

    }
}

.gamesSwiper2 .swiper-slide-thumb-active {
    p {
        font-family: var(--app-text-mobile-font);
        font-size: 18px;
        font-weight: bolder;
        text-align: left;
        color: var(--app-color-gray-dark);

    }

    progress {
        height: 5px;
        background-color: var(--app-color-gray-dark);
    }
}

.adSwiper {
    width: 30%;    
    height: auto;
    margin-bottom: 20px;

}

.adSwiper .swiper-slide img {
    width: 100%;
    object-fit: scale-down;
}

.gamesSwiper3 {
    padding-top: 5px;
    height: 200px;
    width: 100%;
    
    padding-left: 20px;
    padding-right: 20px;
    color: var(--app-color-gray-lighter);
    color: white;
   
    p {
        font-family: var(--app-text-mobile-font);
        font-size: 18px;
        font-weight: bolder;
        text-align: left;
        color: var(--app-color-gray-dark);

    }

    progress {
        height: 3px;
        width: 90%;

    }
}

.gamesSwiper3 .swiper-slide img {
    width: 100%;
    object-fit: scale-down;
}

.gamesSwiper3 .swiper-slide {
    height: 180px;
    width: 460px;
}

.playerSwiper {
    padding-top: 5px;
    /* height: 600px; */
    background-color: transparent;
    z-index: 0;
    p {
        font-family: var(--app-text-mobile-font);
        font-size: 18px;
        font-weight: bolder;
        text-align: left;
        color: var(--app-color-gray-dark);

    }

    progress {
        height: 3px;
        width: 90%;

    }
}

.playerSwiper .swiper-slide img {
    width: 100%;
    object-fit: scale-down;
}

.playerSwiper .swiper-slide {
    width: 400px;
    background-color: transparent;
}

/* Responsive */
@media screen and (max-width: 1000px) {
    .adSwiper {
        height: 240px;
    }
    
}