.selectAndMultiselectHost {
  width: calc(50% - 20px);
  border: 1px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  min-height: 55px;
  font-family: var(--app-text-main-font), serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.selectAndMultiselectHostTiles {
  width: calc(50% - 20px);
  border: 0px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  min-height: 55px;
  font-family: var(--app-text-main-font), serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.section {
  display: flex;
  flex-direction: column;
}

.accordionContainer {
  overflow: hidden;
  margin: 20px 0 20px 0;
  font-family: var(--app-text-main-font), serif;
  font-size: 20px;
  font-weight: 700;

  color: var(--app-color-gray-dark);
  padding: 10px 10px 50px 10px;
  align-items: center;
  border: 1px dashed var(--app-color-gray-light);
  border-radius: 8px;
  outline: none;
  transition: background-color 0.6s ease;

  &[app-variant-has-error="error"] {
    border: 1px solid var(--app-color-error);
  }
}

.active {
  background-color: #FFFFFF;
}

.active .title {
  font-family: var(--app-text-main-font), serif;
}

.customTitleInput {
  border: 1px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  margin-bottom: auto;
  height: 37px;
  font-family: var(--app-text-main-font), serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.icon {
  margin-left: 20px;
  margin-top: auto;
  margin-bottom: auto;
  transition: transform 0.6s ease;
}

.loaderContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 32px;
  max-height: 80%;
  margin: 0 0px 0 5px;

  .host[comp-loading="loading"] & {
    transform: translateX(-5px);
  }
}

.iconsContainer {
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  cursor: pointer;
}

/* Style to rotate icon when state is active */
.rotate {
  transform: rotate(180deg);
}

/* Style the accordion content panel. Note: hidden by default */
.content {
  text-align: left;
  overflow: auto;
  justify-content: center;
  transition: max-height 0.6s ease;
}

.fieldTypeInput {
  ::placeholder {
    font-family: var(--app-text-main-font), serif;
    font-size: 1rem;
    color: var(--app-color-error);
  }

  width: 100%;
  padding-left: 10px;
  border: 0px;
  height: 37px;
  font-family: var(--app-text-main-font),
  serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);
}

input {
  outline: none;
}

.fieldRowContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  height: 32px;
  flex: 1 1;
}

.accordion:hover,
.title {
  font-family: var(--app-text-main-font), serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: 24px;
  text-align: left;
}

.emulatedFlexGap>* {
  margin: 12px 0 0 12px;
}

.emulatedFlexGap {
  padding-top: 10px;
  padding-bottom: 15px;
  display: inline-flex;
  flex-wrap: wrap;
  margin: -12px 0 0 -12px;
  width: calc(100% + 12px);
}

