  font-size: 60px;
}

.teamsLabel {
  font-family: var(--app-text-font-mobile);
  color: var(--app-color-gray-dark);
  font-size: 28px;
}

.scoreLabel {
  width: 140px;
  text-align: center;
  height: 140px;
  font-family: var(--app-text-font-sports);
  color: var(--app-color-gray-dark);
  font-size: 40px;
  margin: 0 10px 0 10px;
  padding: 10px;
  border: 0;

}

.scoreLabelDetail {
  width: 120px;
  text-align: center;
  height: 70px;
  font-family: var(--app-text-font-sports);
  color: var(--app-color-gray-dark);
  font-size: 40px;
  margin: 5px 20px 5px 20px;
  padding: 10px;
}

.teamsLabelDetail {
  font-family: var(--app-text-mobile-font);
  color: var(--app-color-gray-dark);
  font-size: 16px;
}

.timeLabel {
  width: 120px;
  text-align: center;
  font-family: var(--app-text-font-sports);
  color: var(--app-color-gray-dark);
  font-size: 16px;
  margin: 0 10px 0 0px;
  height: 40px;
}


.runningScore {
  padding: 0 20px;
}


.centerDiv {
  flex-direction: row;
  margin: auto;
  justify-content: center;
  justify-items: center;
  align-content: center;
  align-items: center;
  text-align: center;
}





.court {

  fill: var(--app-color-blue);

  stroke: #2B2B2B;
  stroke-width: 4;
  stroke-miterlimit: 10;
}

.zone {

  fill: var(--app-color-blue);
  stroke: #2B2B2B;
  stroke-width: 4;
  stroke-miterlimit: 10;
}

.fullcourt2 {
  justify-content: center;
  justify-items: center;
  align-items: center;
  align-content: center;
  display: flex;
  min-width: 740px;
}

.activePlayers {
  display: flex;
}

.messagePanel {}

.playByPlaySide {}

.mainSideActionControls {}

.scorebaord {
  //min-width: 500px;
  width: 50%;
}

.halfcourt {
  display: none;
}

.lineupNumber {
  display: none;
}

.playerselectedframe {
  width: 50%;
}

.possessionBall {
  display: flex;
}

/* Responsive */
@media screen and (max-width: 900px) {
  .fullcourt2 {
    display: none;
  }

  .scorebaord {
    width: 100%;
    min-width: none;
  }

  .activePlayers {
    display: none;
  }

  .messagePanel {
    display: none;
  }

  .playByPlaySide {
    display: none;
  }

  .mainSideActionControls {
    display: none;
  }

  .teamPicture {
    display: none;
  }

  .halfcourt {
    display: block;
  }

  .lineupNumber {
    display: inline-flex;
  }

  .possessionBall {
    display: none;
  }

  .playerselectedframe {
    width: 100%;
    display: none;
  }
}



/* Tables */

.tableHost {
  table {
    width: 100%;
    border-spacing: 0;
    border-top: 1px dashed #bbb;
    border-left: 1px dashed #bbb;
    border-right: 1px dashed #bbb;
    font-family: var(--app-text-main-font);

    color: black;
    font-weight: 700;

    tr {
      :first-child {
        height: 50px;
      }

      :last-child {
        border-right: 0;
      }
    }

    th {
      :last-child {
        padding: 0 0 0 10px;
        text-align: left;
        min-height: 30px;
        //min-width: 100px;
        color: white;
        background-color: var(--app-color-gray-dark);
        border-right: 1px solid #bbb;

      }
    }

    td {
      text-align: left;
      margin: 0;
      padding: 0.5rem;
      border-right: 1px dashed #bbb;
      border-right: 1px dashed #bbb;
    }
  }

  table tr:hover .action {
    transform: scale(1.3);
  }

  table tr:hover td {
    background: red;
    color: #fbfbfb;
  }
}


.boxCourtTable {
  width: 100%;
  height: 30vh;

  table {
    width: inherit;
    height: inherit;
    border: 1px dashed #bbb;
    font-family: var(--app-text-mobile-font);
    font-size: 14px;
    font-weight: bold;

    th {
      font-size: 14px;
      color: white;
      background-color: var(--app-color-dark-green);
    }

    td {
      font-size: 14px;
      cursor: pointer;
    }

    td:nth-child(3) {
      width: 50px;
    }

    td:nth-child(1) {
      width: 50px;
    }

    tr:hover td {
      background: var(--app-color-gray-lightest);
      color: var(--app-color-gray-darker);
    }

    tr td {
      height: 50px;
      max-height: 50px;
      min-height: 50px;
      padding: 0px;
      margin: 0px;
    }
  }

}

.subTable {
  width: 100%;

  table {
    width: inherit;

    border: 1px dashed black;
    font-family: var(--app-text-mobile-font);
    font-size: 14px;

    th {
      padding: 10px;
      color: white;
      background-color: var(--app-color-dark-green);
    }

    td {
      font-size: 16px;

    }

    tr td {
      height: 40px;
      max-height: 40px;
      min-height: 40px;
      padding: 10px;
      margin: 0px;
    }
  }

}

.detailScoringTable {
  width: 100%;

  table {
    width: inherit;
    border: 1px dashed #bbb;
    font-family: var(--app-text-mobile-font);
    font-size: 14px;

    th {
      padding: 10px;
      color: white;
      background-color: var(--app-color-dark-green);
    }

    tr td {
      height: 40px;
      max-height: 40px;
      min-height: 40px;
      padding: 10px;
      margin: 0px;
    }
  }
}


.endTable {
  width: 100%;
  height: 100%;

  table {
    height: inherit;
    width: inherit;
    border: 1px dashed #bbb;
    font-family: var(--app-text-mobile-font);
    font-size: 14px;
    text-align: center;
    padding: 10px;

    th {
      padding: 10px;
      color: white;
      background-color: var(--app-color-dark-green);
    }

    tr td {
      height: 40px;
      max-height: 40px;
      min-height: 40px;
      padding: 2px;
      margin: 0px;
      justify-items: center;
    }
  }
}

.boxScoreTable {
  width: 100%;
  overflow: auto;

  table {
    width: inherit;
    height: inherit;
    border: 1px dashed #bbb;
    font-family: var(--app-text-mobile-font);
    font-size: 12px;
    font-weight: bold;
    text-align: left;

    th {
      color: white;
      background-color: var(--app-color-dark-green);
      font-size: 12px;
      padding: 10px 3px;
    }

    td {
      cursor: pointer;
      font-size: 12px;

    }

    tr td {
      height: 50px;
      max-height: 50px;
      min-height: 50px;
      padding: 0px;
      margin: 0px;
    }
  }

}


.leagueTable {
  width: 100%;

  table {
    width: inherit;
    border: 1px dashed #bbb;
    font-family: var(--app-text-mobile-font);

    tr {
      width: 100px;

      :last-child {}
    }


    tr:nth-child(even) {
      background-color: var(--app-color-gray-lightest);
    }

    th {
      padding: 0 2px 0 2px;

      color: white;
      background-color: var(--app-color-dark-green);
      border: 1px dashed #bbb;

      :first-child {
        font-size: 16px;
        text-align: left;
        padding: 6px;
      }
    }

    td {
      text-align: left;
      font-size: 16px;
      padding: 6px;

    }
  }
}

.scoringByPeriodTable {
  width: 100%;

  table {
    width: inherit;
    font-family: 'Arial';
    font-size: 14px;
    text-align: center;

    th:nth-child(1) {
      text-align: left;
    }

    tr td:nth-child(1) {
      text-align: left;
      border: 0px solid black;
    }

    tr td {
      height: 30px;
      border: 1px solid black;
    }
  }
}

.detailScoringTablePrint {
